bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.project.users", {}, {
    /*
     *   Constructor
     */
    init: function (workportalFacade, dataService, relatedusers, params) {
        var self = this;
        self.relatedusers = relatedusers;

        // Call base
        self._super(workportalFacade, dataService, params);

        self.contextsSidebarActivity = params.contextsSidebarActivity;

        self.plugins = {};
        self.users = [];
        self.loadedUsers = false;

        //Load templates
        self.loadTemplates({
            "project-users": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.users").concat("#project-users-main")
        });
    },

    /*
     * Renders the template defined in the widget
     */
    renderContent: function () {
        var self = this;
        self.content = $("<div></div>");
        return self.content;
    },

    /*
     * links events with handlers
     */
    postRender: function () {
        var self = this;

        //Handlers
        self.contextsSidebarActivity.forEach(function(context){
            self.sub(context, $.proxy(self.updateView, self));
        });
        self.sub('ON_RENDER_FINISH', $.proxy(self.onRenderFinish, self));
    },

    updateView: function (event, params) {
        var self = this;
        self.params = params;
        setTimeout(function(){
            if(self.loadedUsers === false){
                self.onRenderFinish();
            }
        }, 500);
    },

    onRenderFinish: function(){
        var self = this;
        var args = self.params.args;
        var $content = self.getContent().empty();
        self.loadedUsers = true;

        var contentTemplate = self.getTemplate("project-users");
        contentTemplate.render().appendTo($content);
        self.showCreatorInformation($content);

        var typesAssignUser = [{
            id: args.createdBy.userId,
            typeName: "owner"
        }];

        if (args.idCase > 0) {
            self.relatedusers.render(self.content, ".relatedusers-wrapper", args.idCase, typesAssignUser);
        }
    },

    /**
     * show the information of the creator case
     * @param {string} $content html conteiner
     */
    showCreatorInformation: function ($content) {
        var self = this;
        var params = {
           userIds: self.params.args.createdBy.userId,
           width: 50,
           height: 50
        };
  
        self.dataService.getUsersData(params).done(function (user) {
            var userInfo = usersAdapter.createJsonUserInfo(
                user[0].id,
                user[0].name,
                user[0].username,
                user[0].picture?"data:image/png;base64,"+user[0].picture:undefined,
                user[0].email,
                user[0].name.getInitials(),
                false,
                true,
                [],
                []
                );
            $content.find("#ui-bizagi-wp-project-users-wrapper .ui-bizagi-wp-project-users-creator-info").userinformation(self, { user: userInfo });
        });
    },

    clean: function () {
        var self = this;
        self.loadedUsers = false;
        self.contextsSidebarActivity.forEach(function(context){
            self.unsub(context, $.proxy(self.updateView, self));
        });
    }
});

bizagi.injector.register("bizagi.workportal.widgets.project.users", ["workportalFacade", "dataService", "relatedusers", bizagi.workportal.widgets.project.users]);