/*
 *   Name: Bizagi Workportal Desktop Project Subprocesses Data Tooltip
 *   Author: Bizagi Latam
 */

bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.project.subprocesses.data", {
    
    responseSubprocessState: {},
    responseSubprocess: function(newValue){
        if(newValue){
            responseSubprocessState = newValue;
        }
        return responseSubprocessState;
    },

    auxArrayListSubprocessesState: [],
    auxArrayListSubprocesses: function(newItem){
        if(newItem){
            if(jQuery.isArray(newItem) && newItem.length === 0){
                this.auxArrayListSubprocessesState.length = 0; //clean array if change reference
            }
            else{
                return this.auxArrayListSubprocessesState.push(newItem);
            }
        }
        else{
            return this.auxArrayListSubprocessesState;
        }
    }
}, {
    /*
     *   Constructor
     */
    init: function (workportalFacade, dataService, params) {
        var self = this;

        // Call base
        self._super(workportalFacade, dataService, params);

        // Init static properties
        self.Class.auxArrayListSubprocesses([]);

        //Load templates
        self.loadTemplates({
            "project-subprocesses-tootip-custom-properties": bizagi.getTemplate("bizagi.workportal.desktop.widgets.project.subprocesses.data").concat("#project-subprocesses-tooltip-custom-properties-wrapper")
        });
    },


    initializeTooltip: function (selectorTooltip) {
        var self = this;
        var $content = self.getContent();

        $(selectorTooltip, $content).tooltip({
            position: {
                collision: "flipfit",
                my: "right center", at: "left-10 center"
            },
            show: null, // show immediately
            content: function () {

                var idCase = $(this).closest("li").find("#idCase").val();
                var itemsFinded = self.Class.auxArrayListSubprocesses().filter(function(item){
                    return item.idCase == idCase;
                });

                var indexSubprocess, idCustData, text;
                if(itemsFinded.length > 0){
                    indexSubprocess = itemsFinded[0].indexSubprocess;
                    idCustData = itemsFinded[0].idCustData;
                    text = $(this).text();

                    return self.getContentTooltip(indexSubprocess, idCustData, text,
                        self.getTemplate("project-subprocesses-tootip-custom-properties"));
                }

                
            },
            open: function (event, ui) {
                if (typeof (event.originalEvent) === 'undefined') {
                    return false;
                }
                var $id = $(ui.tooltip).attr('id');
                // close any lingering tooltips
                $('div.ui-tooltip').not('#' + $id).remove();
            },
            close: function (event, ui) {
                ui.tooltip.hover(function () {
                    $(this).stop(true).fadeTo(400, 1);
                },
                    function () {
                        $(this).fadeOut('400', function () {
                            $(this).remove();
                        });
                    });
            }
        });
    },

    getContentTooltip: function (indexSubprocess, idCustData, textLI, templateTooltip) {
        var self = this;

        //when not show custom columns
        if (idCustData == "-1") {
            idCustData = "0";//So show only basic data subprocess
        }

        var rowCustomProperty = function (nameProperty, valueProperty) {
            return {
                nameProperty: nameProperty,
                valueProperty: valueProperty
            };
        };

        var arrayCustomsProperties = [];
        var auxValueProperty = "";
        var subprocessData = self.getSubprocessData(indexSubprocess, idCustData);
        var custData = subprocessData.custData;
        var custDataTypes = subprocessData.custDataTypes;
        var custFields = subprocessData.custFields;
        var displayName = subprocessData.displayName;

        //subproceso
        arrayCustomsProperties.push(new rowCustomProperty(bizagi.localization.getResource("workportal-widget-inboxcommon-subprocess"), displayName));

        //Others custom properties
        if (custData.length) {
            for (var i = 0; i < custData.length; i += 1) {
                auxValueProperty = custData[i];
                if (custDataTypes) {
                    if (custDataTypes[i] == "Money") {
                        auxValueProperty = bizagi.util.formatMonetaryCell(auxValueProperty);
                    } else {
                        if (custDataTypes[i] == "Boolean") {
                            auxValueProperty = bizagi.util.formatBoolean(auxValueProperty);
                        } else {
                            if (custDataTypes[i] == "Float" || custDataTypes[i] == "Real") {
                                auxValueProperty = bizagi.util.formatDecimalCell(auxValueProperty);
                            }
                            else {
                                if (typeof auxValueProperty === "function") {
                                    auxValueProperty = "";
                                }
                            }
                        }
                    }
                }
                arrayCustomsProperties.push(new rowCustomProperty(custFields[i], auxValueProperty));
            }
        }

        //Render properties
        var objectTemplate = {};
        objectTemplate.arrayCustomsProperties = arrayCustomsProperties;
        objectTemplate.nameSubprocess = textLI;
        var contentTemplate = templateTooltip;
        var contentTableCustomProperties = $('<div class="bizagi-custom-tooltip-subprocesses"></div>');
        contentTemplate.render(objectTemplate).appendTo(contentTableCustomProperties);

        return contentTableCustomProperties.html();
    },

    getSubprocessData: function (indexSubprocess, idCustData) {
        var self = this;
        var subProcessData = {};
        var subProcess = self.Class.responseSubprocess().subProcesses;
        var subProcPersonalized = self.Class.responseSubprocess().subProcPersonalized;

        if (subProcess && subProcess.length > 0) {
            subProcessData.custData = subProcess[indexSubprocess].custData;
            subProcessData.custDataTypes = subProcess[indexSubprocess].custDataTypes;
            var customFields = self.Class.responseSubprocess().CustFields[idCustData];
            subProcessData.custFields = customFields[Object.keys(customFields)[0]];
            subProcessData.displayName = subProcess[indexSubprocess].displayName;
        } else if (subProcPersonalized && Object.keys(subProcPersonalized).length > 0) {
            subProcessData.custData = subProcPersonalized[0].subProcesses[indexSubprocess].custData;
            subProcessData.custDataTypes = subProcPersonalized[0].subProcesses[indexSubprocess].custDataTypes;
            subProcessData.custFields = subProcPersonalized[0].CustFields[0];
            subProcessData.displayName = subProcPersonalized[0].subProcesses[indexSubprocess].displayName;
        }
        return subProcessData;
    },


    clean: function () {

    }
});

bizagi.injector.register("bizagi.workportal.widgets.project.subprocesses.data", ["workportalFacade", "dataService", bizagi.workportal.widgets.project.subprocesses.data]);