bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.project.activity.description", {}, {

    /*
     *   Constructor
     */
    init: function (workportalFacade, dataService, params) {
        var self = this;

        // Call base
        self._super(workportalFacade, dataService, params);

        params = params || {};

        //Load templates
        self.loadTemplates({
            "project-activity-description": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.activity.description").concat("#project-activity-description")
        });
    },

    /**
     * Renders the template defined in the widget
     */
    renderContent: function () {
        var self = this;
        var template = self.getTemplate("project-activity-description");
        var description = self.getWorkitemDescription();

        if (description) {
            self.content = template.render({ description: description });
        } else {
            self.content = $('<div />');
        }

        return self.content;
    },

    /**
     * links events with handlers
     */
    postRender: function () {
        
    },

    getWorkitemDescription: function () {
        var self = this;
        var params = self.params || {};
        var idWorkitem = params.idWorkitem || 0;
        var currentState = params.currentState || [];
        var description = "";

        for (var i = 0; i < currentState.length; i++) {
            if (idWorkitem == currentState[i].idWorkItem) {
                description = currentState[i].tskDescription;
                break;
            }
        }

        return description;
    },
    
    /**
     * Clean widget
     */
    clean: function () {
        
    }
});

bizagi.injector.register('bizagi.workportal.widgets.project.activity.description', ['workportalFacade', 'dataService', bizagi.workportal.widgets.project.activity.description], true);
